/**
 * DwD - Reldens Interface - Styles
 */
body { font-family:Verdana, Geneva, sans-serif; margin:0; padding:0; }
.wrapper,
.header,
.footer,
.content
{ float:left; position:relative; display:block; width:100%; margin:0; padding:0; min-width:300px; }
.wrapper, .game-container { height:100%; }
.header,
.footer
{ background-color:#222222; color:#ffffff; }
.header { height:10%; min-height:46px; }
.footer { height:6%; min-height:20px; }
.content { height:84%; }
.game-container { background-color:#000; }
.header h1 { float:left; position:relative; display:block; width:96%; margin:1% 2%; padding:0; text-align:center; }
.col-2 { float:left; display:block; position:relative; width:50%; margin:0; padding:0; }
.footer-content { position:relative; display:block; width:100%; max-width:1000px; margin:20px auto 0; padding:0; }
.copyright { position:relative; display:block; width:100%; min-height:20px; margin:0; padding:0; text-align:center; }
.copyright a, .copyright a:hover, .copyright a:visited { display:block; margin:1% 0 0; color:#fff; text-decoration:none; }
#login_form,
#register_form
{ max-width:400px; display:block; margin:0 auto; }
.response-error { color:#ff0000; }
.player { width:100px; height:100px; position:absolute; padding-top:24px; box-sizing:border-box; left:0; top:0; }
.ux-box { z-index:1001; display:block; margin:0; padding:0; }
.ux-box-left { /* position:absolute; bottom:10px; left:0; max-width:150px; */ background-color: rgba(0, 0, 0, 0.5); }
.ux-box-left button { float:left; }
.ux-box-left .vertical-buttons { float:left; width:56px; text-align:center; top:0; position:relative; }
.ux-box-left #left,.ux-box-left #right { position:relative; top:21px; }
.ux-box-left .vertical-buttons button { float:none; }
.ux-box-left .vertical-buttons #down { margin-top:23px; }
.ux-box-right { /* position:absolute; top:0; right:0; */ background-color: rgba(0, 0, 0, 0.5); }
.player-name { margin:10px 0; text-align:center; color:#ffffff; }
